@use '../variables/variables' as *;
.pizza_components_wrapper {
  margin-top: 20px;
  margin-bottom: 20px;
}
.food-badge-block {
    color: $main_red;
    text-transform: uppercase;
    position: relative;
    width: 50px;
    height: 50px;
    display: block;
    z-index: 10;
}
.food-badge {
    background: #fff;
    width: 50px;
    height: 50px;
    top: 0;
    left: 0;
    position: absolute;
    text-align: center;
    z-index: -1;
  
  }
  .food-badge:before,
  .food-badge:after {
    content: "";
    position: absolute;
    top: 0;
    left: 0;
    height: 50px;
    width: 50px;
    background: #fff;
  }
  .food-badge:before {
    transform: rotate(30deg);
  }
  .food-badge:after {
    transform: rotate(60deg);
  }
  //buttons
  .component-buttons {
    position: absolute;
    top: 0;
    right: -15px;
  }
  //nav
  .pizza-components-nav {
      background-color: #EAEAEA;
      margin-bottom: 25px;
      ul {
          list-style: none;
          display: flex;
          padding: 4px;
          height: 50px;
          margin: 0;
           li {
            flex: 1;
            text-align: center;
         
            a {
                background-color: #EAEAEA;
                color: $main_dark;
                text-decoration: none;
                font-weight: bold;
                font-size: 14px;
                line-height: 16px;
                display: flex;
            align-items: center;
            justify-content: center;
            height: 100%;
            }
            a.active {
                background-color: $main_dark;
                color: #fff;
            }
          }
      }
  }
  //fancybox
  .pizza-layers-block, .pizza-floors-block {
    display: grid;
    grid-template-columns: repeat(3, 1fr);
    grid-gap: 15px;
    flex: 0 0 63%;
    padding-right: 25px;
    border-right: 2px solid #EAEAEA;
    
  }
  .pizza-layers-selected, .pizza-floors-selected{
    flex: 0 0 33%;
    padding-left: 27px;
  }
  //buttons
  .ev-pizza-button {
    background-color: #FF0329;
    color: #fff;
    display: block;
    width: 100%;
    cursor: pointer;
    border: 0;
    outline: 0;
    padding: 15px;
    text-align: center;
    text-transform: uppercase;
    font-weight: bold;
    transition: background-color 0.3s ease;
  }
  .ev-pizza-button:hover {
    background-color: #cc0725; 
  }
  .ev_pizza_total {
    display: flex;
    align-items: center;
    margin-bottom: 30px;
    & > span:first-of-type {
      color: #666666;
      font-weight: bold;
      font-size: 20px;
      display: block;
      
    }
    & > span:nth-of-type(2) {
      color: $main_dark;
      font-weight: bold;
      font-size: 30px;
      display: block;
      margin-left: 10px;
      flex: 1;
    }
  }
  .pizza-highlight {
    color: $main_red;
  }
  .ev-pizza-title, .u-pizza-title {
    color: $main_dark;
    font-size: 14px;
line-height: 16px;
font-weight: bold;
text-transform: uppercase;
margin-bottom: 10px;
  }
  .ev-pizza-price, .u-pizza-price {
    color: #666666;
    font-size: 15px;
line-height: 18px;
font-weight: bold;
text-transform: uppercase;
  }
  .ev-pizza-weight, .u-pizza-weight {
    color: #666666;
    font-size: 15px;
line-height: 18px;
  }
  .pizza-layer-item, .pizza-floor-item {
    position: relative;
    display: flex;
    flex-direction: column;
    align-items: center;
  }
  .pizza-components-buttons {
    display: flex;
    align-items: center;
    margin-top: 20px;
    & > button:first-of-type {
      margin-right: 15px;
    }
  }
  .woocommerce div.product p.price, .woocommerce div.product span.price {
    // color: #fff;
    font-size: 30px;
line-height: 35px;
text-transform: uppercase;
  }
  .ev-remove-component, .u-remove-component {
    background: rgba(255,255,255,0.9);
    border: 1px solid #EBEBEB;
    display: flex;
    align-items: center;
    justify-content: center;
    height: 38px;
    width: 38px;
    text-decoration: none;
    border-radius: 50%;
    position: absolute;
    right: -15px;
    top: 0;
    transition: all 0.2s ease;
  }
  .pizza-components-item .ev-remove-component path, .pizza-components-item .u-remove-component path {
    stroke: #FF0329;
  }
  .pizza-components-item.active .ev-remove-component path, .pizza-components-item.active .u-remove-component path {
    stroke: rgb(29, 168, 29);
    // background-color: rgba(46, 179, 46, 0.8);
  }
  .ev-remove-component path,  .u-remove-component path{
  
  stroke-linecap: round;
  stroke-linejoin: round;
  }
  .pizza-components-item.active .ev-remove-component path, .pizza-components-item.active .u-remove-component path {
    d: path("M10,2 L10,18 M18,10 L2,10");
  }
  .ev-pizza-fancy-ingredients {
    color: $main_dark;
	min-width: 560px;
    ul {
      list-style: none;
      margin: 0;  
      li {
        strong {
          display: block;
          color: $main_dark;
          text-transform: uppercase;
          font-size: 14px;
          margin-bottom: 5px;
        }
        p {
          position: relative;
          display: inline-flex;
          flex-direction: column;
          justify-content: center;
          align-items: center;
          background-color: rgb(120, 124, 130);
          color: #fff;
          padding: 2px 20px;
          font-size: 12px;
          cursor: pointer;
          margin-right: 8px;
          margin-bottom: 8px;
          user-select: none;
          vertical-align: middle;
          
          border-radius: 40px;
          box-sizing: border-box;
		  margin: 0;
          margin-bottom: 20px;
        }
      }
    }
  }
  .pizza-quantity-badge {
    position: absolute;
    top: 0;
    right:-5px;
    border-radius: 50%;
    background-color: hotpink;
    color: #fff;
    padding: 5px;
    display: flex;
    align-items: center;
    justify-content: center;
    box-sizing: border-box;
    width: 20px;
    height: 20px;
  }
  #remove-component .pizza-components-item {
    cursor: pointer;
  }
  .pizza-composition-toggle {
    cursor: pointer;
  }
  .woocommerce-variation-add-to-cart-disabled .pizza_components_wrapper {
    user-select: none;
    opacity: 0.8;

  }
  .woocommerce-variation-add-to-cart-disabled .pizza_components_wrapper .ev-pizza-button:hover,  .woocommerce-variation-add-to-cart-disabled .pizza_components_wrapper .u-pizza-button:hover{
    background-color: #FF0329; 
  }
  .pizza-layers-selected__footermobile, .pizza-floors-selected__footermobile {
    display: none;
  }
  //tippy
  .pizza-tippy {
    position: absolute;
    left: 5px;
    top: 5px;
    cursor: pointer;
  }
  .single-product div.product {
    overflow: visible;
  }
  //ripple
  .ripple {
    position: absolute;
    top: 0;
    left: 0;
    bottom: 0;
    right: 0;
    overflow: hidden;
    -webkit-transform: translateZ(0);
            transform: translateZ(0);
    border-radius: inherit;
    pointer-events: none;
  }
  
  .rippleWave {
    -webkit-backface-visibility: hidden;
            backface-visibility: hidden;
    position: absolute;
    border-radius: 50%;
    -webkit-transform: scale(0.7);
        -ms-transform: scale(0.7);
            transform: scale(0.7);
    background: rgb(216, 216, 216);
    opacity: 0.45;
    -webkit-animation: ripple 2s forwards;
            animation: ripple 2s forwards;
  }

  
  @keyframes ripple-shadow {
    0% {
      -webkit-box-shadow: 0 0 0 transparent;
              box-shadow: 0 0 0 transparent;
    }
    20% {
      -webkit-box-shadow: 0 4px 16px rgba(0, 0, 0, 0.3);
              box-shadow: 0 4px 16px rgba(0, 0, 0, 0.3);
    }
    100% {
      -webkit-box-shadow: 0 0 0 transparent;
              box-shadow: 0 0 0 transparent;
    }
  }
  
  @keyframes ripple {
    to {
      -webkit-transform: scale(12);
              transform: scale(12);
      opacity: 0;
    }
  }
  @media(max-width: 990px) {
    .pizza-tippy {
      display: none;
    }
  }
  @media(max-width: 767px) {
    .pizza-tippy {
      display: none;
    }
    .pizza-layers-block, .pizza-floors-block {
		flex: 1;
      display: block;
      width: 100%;
      padding-right: 0;
      border: none;
      margin-top: 15px;
    }
    .pizza-layers-selected, .pizza-floors-selected {
      padding-left: 0;
	  flex: 1;
    }
    .pizza-fancybox-layers, .pizza-fancybox-sides, .pizza-fancybox-floors {
      flex-direction: column-reverse;
    }
    .pizza-layers-selected__footermobile, .pizza-floors-selected__footermobile {
      display: block;
      margin-top: 15px;
    }
    .ev-pizza-title, .u-pizza-title {
      display: block;
      text-align: center;
    }
    .ev-pizza-price, .ev-pizza-weight, .u-pizza-price, .u-pizza-weight {
      display: block;
      text-align: center;
    }

	.pizza-components-tabs .slick-track {
		display: flex !important;
	}
	.pizza-components-tabs .pizza-components-item {
		height: auto !important;
	}
  }
  @media(max-width: 576px) {
    .ev-pizza-title, .u-pizza-title {
      font-size: 12px;
    }
    .ev-pizza-price, .ev-pizza-weight, .u-pizza-price, .u-pizza-weight {
      font-size: 12px;
    }
    .ev_pizza_total>span:nth-of-type(2), .u_pizza_total>span:nth-of-type(2) {
      font-size: 18px;
    }
	.ev-pizza-fancy-ingredients{
		min-width: 100%;
	}

	.ev-pizza-button {

		padding: 10px 15px;
		font-size: 14px;
	  }
  }
