@use '../variables/variables' as *;

.pizza-fancybox {
    color: $main_dark;
    padding: 27px;
	width: 1500px;
	max-width: 100%;
}
.pizza-fancybox-layers, .pizza-fancybox-sides, .pizza-fancybox-floors {
    color: $main_dark;
    display: flex;
}
.pizza-fancybox-floors .slimScrollDiv, .pizza-fancybox-sides .slimScrollDiv{
    flex: 0 0 63%;
}
.pizza-fancybox-sides .pizza-floor-item img {
    max-width: 150px;
}
.pizza-fancybox-sides .pizza-floor-item > p {
    margin:0;
}

.pizza-layers-block.swiper-initialized {
	padding-bottom: 30px;

	.swiper-pagination {
		bottom: 0;
	}
}

.pizza-layers-selected, .pizza-floors-selected {
	&__header {
		span {
			text-transform: uppercase;
			font-size: 26px;
			font-weight: bold;
		}
		margin-bottom: 40px;
	}
	&__block {

	}
	&__item {
		position: relative;
		display: flex;
		align-items: center;
		background: #FFFFFF;
		box-shadow: 2px 2px 10px rgba(193, 193, 193, 0.4);
		padding: 15px;
		column-gap: 6px;
		.ev-remove-layer, .ev-remove-side, .u-remove-floor,.u-remove-side {
			position: absolute;
			right: 10px;
			top: 10px;
			cursor: pointer;
		}
		.pizza-layers-left, .pizza-floors-left {
			flex: 0 0 33%;
		}
		.pizza-layers-right, .pizza-floors-right {
			flex: 0 0 66%;
			text-align: center;
			& > span:first-of-type {
				color: $main_dark;
				font-weight: bold;
				font-size: 18px;
				line-height: 21px;
				display: block;
				margin-bottom: 16px;
				text-transform: uppercase;
			}
			& > span:nth-of-type(2) {
			
				color: #666666;
				font-weight: bold;
				font-size: 18px;
				line-height: 21px;
				display: block;
				
			}
			& > span.pizza-text-placeholder {
				color: #DDDDDD;
				text-transform: uppercase;
			}
		}
	}

	&__item:not(:last-of-type) {
		margin-bottom: 20px;
	}

	&__footer {
	margin-top: 30px;
	}
}

@media (max-width: 1350px) {
	.pizza-fancybox {
		width: 1200px;
	}
	.pizza-layers-selected, .pizza-floors-selected {
		
		&__item {
			
			.pizza-layers-right, .pizza-floors-right {
				
				& > span:first-of-type {
					
					font-size: 16px;
					line-height: 18px;
					
					margin-bottom: 12px;
			
				}
				& > span:nth-of-type(2) {
				
			
					font-size: 16px;
					line-height: 18px;
					
					
				}
			
			}
		}
	}
}

@media(max-width:767px) {
    .pizza-layers-selected,  .pizza-floors-selected{

		&__item {
			.pizza-layers-right, .pizza-floors-right {
				
			}
			.pizza-layers-left, .pizza-floors-left {
				
				img {
					max-width: 100px;
				}
			}
		}

        &__footer {
           display: none;
         }
    }
}
@media (max-width: 500px) {
    .pizza-layers-selected, .pizza-floors-selected {
    .pizza-layers-right, .pizza-floors-right {
        & > span:first-of-type {
        
            font-size: 14px;
            line-height: 18px;           
          }
          & > span:nth-of-type(2) {       
            font-size: 14px;
            line-height: 18px;

          }
    }
}
}
