.pizza-component-tabs-wrapper {

	h3 {
		margin-bottom: 10px;
	}

    .pizza-tab-nav {
        display: flex;
        list-style: none;
        align-items: center;
		list-style: none;
		padding: 0;
        margin: 0;
        li {
            a {
                display: block;
                padding: 5px;
                height: 60px;
                min-width: 80px;
                background-color: #fff;
                border: 1px solid #E8E8E8;
                transition: all 0.3s ease;
                img {
                    display: block;
                    height: 100%;
                    margin: 0 auto;
                }
            }
            a:hover {
                border-color: #868686;
            }
            a.active {
                border-color: #868686;  
            }
            
        }

        li:first-of-type {
            a {
            border-radius: 6px 0 0 6px;
            }
        }
        li:last-of-type {
            a {
            border-radius:  0 6px 6px 0;
            }
        }
    }
    .tab-components-wrapper {
        margin-top: 30px;
    }
    .component-item-tab {
        display: flex;
        flex-wrap: wrap;
        align-items: flex-start;
       	height: 0;
        opacity: 0;
		column-gap: 10px;
        visibility: hidden;
        transition: transform 0.3s ease;
        transform: translateY(13px);
    }
    .component-item-tab.fade-in {
        height:auto;
        opacity: 1;
        visibility: visible;
        transform: translateY(0);
    }

	.tab-components-wrapper {
		.component-item   {
			p {
				font-size: 13px;
				text-align: center;
				color: #464646;
			
				margin:0 !important;
			}

			.component-price {
				font-weight: 500;
			}
	
		 }
	}


}

