.pizza-smooth-radio {
	border-radius: 9999px;
    background-color: rgb(243, 243, 247);
    height: 32px;
    display: flex;
    position: relative;
    overflow: hidden;
    margin: 8px 0px;
    transform: translateZ(0px);

	&.smooth-selected {

		.ev-smooth-overlay {
			transform: translateX(calc(0%));
			transition: transform 200ms ease-out 0s, width;
			padding: 2px;
		
			&::before {
				content: " ";
				width: 100%;
				height: 100%;
				display: block;
				background-color: rgb(255, 255, 255);
				border-radius: 9999px;
				box-shadow: rgba(6, 5, 50, 0.19) 0px 6px 20px;
			
			}
		}	

	}

	.ev-smooth-overlay {
		position: absolute;
		top: 0px;
		left: 0px;
		bottom: 0px;
	}


	&.smooth-col-2 {
		.ev-smooth-overlay {
			width: calc(50%);
		}
	}
	
	&.smooth-col-3 {
		.ev-smooth-overlay {
			width: calc(33.3333%);
		}
	}
	&.smooth-col-4 {
		.ev-smooth-overlay {
			width: calc(25%);
		}
	}
	&.smooth-col-5 {
		.ev-smooth-overlay {
			width: calc(20%);
		}
	}

}



.ev-smooth-item {
	display: block;
    flex: 1 1 0%;
    position: relative;
    cursor: pointer;
    user-select: none;
    border-radius: 9999px;
    color: rgb(0, 0, 0);
    font-size: 12px;
    line-height: 32px;
    font-weight: normal;
    text-align: center;
    height: 32px;
    transition: color 150ms ease 0s;

	&.disabled {
		color:rgb(171, 173, 186);
		cursor: not-allowed;
	}
}

.ev-smooth-label {

}

.fancybox__content .ev-smooth-item:focus {
	outline: 0 !important;
}