@use '../variables/variables' as *;
.pizza-dodo-style {
	.pizza-base-components {
		display: flex;
		
		flex-wrap: wrap;

		& > .pizza-component-name {
			
			font-size: 14px;
			line-height: 20px;
			font-weight: 400;
			display: flex;
			align-items: center;
			margin-left: 5px;
			color: $main_dark;
		}
	}
	.pizza-base-item {
		background: none;
		padding: 0;
		min-height: 0;
		outline: 0;
		box-shadow: none;
		border:0;
		border-radius: 0;
		

		font-size: 14px;
		line-height: 20px;
		cursor: pointer;
		position: relative;
		display: flex;
		align-items: center;
		column-gap: 3px;
		.pizza-component-name {
			text-decoration: underline dotted;
			text-underline-offset: 3px;
			font-size: 14px;
			line-height: 20px;
			font-weight: 400;
			color: $main_dark;
		}
		.pizza-component-icon {
			display: flex;
			border: 1px solid #000;
			width: 14px;
			height: 14px;
			justify-content: center;
			align-items: center;
			vertical-align: middle;
			
			border-radius: 50%;
			svg {
				
				width: 8px;
				height: 8px;
			}
		}
	}
	.pizza-base-item:not(:first-of-type) {
		margin-left: 5px;
	}
	.pizza-base-item.removed {
		.pizza-component-name {
			text-decoration: none;
			position: relative;
		}
		.pizza-component-name::before {
			content: '';
			width: 100%;
			position: absolute;
			right: 0;
			
			top: calc( 50% );
			border-bottom: 1px solid #222222;
		}
	}

	& > span {
		display: block;
		font-weight: bold;
		font-size: 20px;
		line-height: 24px;
		margin-bottom: 12px;
		margin-top: 14px;
	}
}