.woocommerce form.cart .pizza_components_wrapper {
    .component-img {
        position: relative;
        width: 68px;
        height: 68px;
        background: white no-repeat center;
        background-size: contain;
    }
    .component-buttons {
        color: #000;
       & .pizza-quantity {
            display: flex;
            margin-right: 0;
            border-radius: 50px;
            float: unset;
            margin-right: 0 !important;
            background: rgba(255, 255, 255, 0.9);
            border: 1px solid #EBEBEB;
        }
        &  .qty_button_plus {
  
           
         }
         &  .qty_button_minus {
            display: none;
         }
         button {
			display: flex;
			align-items: center;
			justify-content: center;
            font-size: 25px;
            z-index: 1;
            border-radius: 50%;
            width: 38px;
            height: 38px;
            position: relative;
            z-index: 10;
            font-weight: 400;
            border: 0;
            outline: 0;
            cursor: pointer;
            background: none;
            color: #949599;
            padding: 0;
			svg {
				fill: #949599;
			}
         }
         button:hover {
            color: #58ce47;
			svg {
				fill: #58ce47;
			}
         }
         input[type=number] {
            width: 22px;
            height: 38px;
            font-size: 14px;
            line-height: 16px;
            border: 0;
            pointer-events: none;
            background: none;
            padding: 0;
            text-align: center;
             color: #303030;
             z-index: 10;
             position: relative;
             border: 0;
             outline: 0;
             display: none;
             -webkit-appearance: none;
             -moz-appearance: none;
             appearance: none;
         }
         input[type=number].is-active {
            display: inline-block;
         }
         input[type=number]::-webkit-inner-spin-button, input[type=number]::-webkit-outer-spin-button {
            -webkit-appearance: none;
            -moz-appearance: none;
            appearance: none;
         }
    }
} 


