@use '../variables/variables' as *;

.pizza-components-tab {
    display: grid;
    grid-template-columns: repeat(3, 1fr);
    grid-gap: 15px;
   height: 0;
    opacity: 0;
    
    visibility: hidden;
    transition: transform 0.3s ease;
    transform: translateY(13px);
}
.pizza-components-tab.fade-in {
    height:auto;
    opacity: 1;
    visibility: visible;
    transform: translateY(0);
}
.pizza-components-item {
    position: relative;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: space-between;
    background-color: #EAEAEA;
    padding: 10px;
	



	.pizza-component-name, .pizza-component-price, .pizza-component-weight {
		font-size: 14px;
		line-height: 18px;
		font-weight: bold;
		color: $main_dark;
	}
}

.pizza-components-tab[data-id=remove-component] .pizza-components-item  {
	border: 1px solid transparent;
	&.active {
		border: 1px solid $main_red;
		background-color: #fff;
	}
}


body.woocommerce .pizza_components_wrapper .pizza-components-block {
	.pizza-component-meta {
		margin: 0;
		margin-top: auto;
	}
}

@media(max-width: 990px) {
    .pizza-components-tab {
        display: block;
    }
    .pizza-component-meta{
        display: flex;
        flex-direction: column;
        align-items: center;
    }
    .pizza-component-name {
        display: block;
        text-align: center;
    }
    .pizza-components-item:not(:last-of-type) {
        margin-right: 10px;
    }

	.slick-slider .pizza-components-item {
		display: flex;

		.pizza-component-name {
			margin-bottom: 6px;
		}
	}
}

@media (max-width: 576px) {
	.pizza-components-item {
		.pizza-component-image {
			max-width: 130px;
		}
	}

	.pizza-components-tab.swiper.fade-in {
		padding-bottom: 30px;

		.swiper-pagination {
			bottom: 0;
		}
	}
}


