.pizza-components-wrapper {
   

	.components-item-wrapper {
		display: flex;
		flex-wrap: wrap;
		align-items: flex-start; 
		
		
		.component-item   {
			p {
				font-size: 13px;
				text-align: center;
				color: #464646;
			
				margin:0 !important;
			}

			.component-price {
				font-weight: 500;
			}
	
		 }
	}

	.component-item {
		display: flex;
		flex-direction: column;
		align-items: center;
		width: 100%;
		max-width: 72px;
		margin-right: 10px;
	}

}

.pizza-quick-view {
	.pizza-components-wrapper {
   

		.components-item-wrapper {
		max-width: 400px;
		}
	}
}

