.wc-item-meta .wc-item-food {
    float:unset;
    }
    .wc-item-meta .wc-item-food ~ p {
        padding-left: 20px;
    }
    
    .widget_shopping_cart ul li .variation {
        display: block;
        /* float: left; */
    }
    .widget_shopping_cart ul li .variation p {
        margin-bottom: 0;
    }
    .widget_shopping_cart ul li .variation {
        font-size: 14px;
    }
    
    .wc-item-meta .wc-item-meta-label, .wc-item-meta dt, dl.variation .wc-item-meta-label, dl.variation dt {
        float: left;
        clear: both;
        margin-right: 0.25rem;
        margin-top: 0;
        list-style: none outside;
        
    }
    .wc-item-meta dd, dl.variation dd {
        margin: 0;
    }
    
    .food-components-wrapper h3 {
        font-size: 20px;
        margin-bottom: 0;
    }
    .shop_table .product-name dl.variation dt, .shop_table .product-name dl.variation dd, .shop_table .product-name dl.variation p {
        text-transform: none;
        display: block;
    }
    body .shop_table.cart .product-name .product-title {
        display: block;
    }
    .shop_table .product-name .product-name-space {
        margin-right: 0;
    }
    .shop_table .product-name dl.variation {
        display: block;
    }
    
    
    
    @media (max-width:767px) {
      
    
        .woocommerce form.cart .component-buttons .pizza-quantity {
            max-width: 120px;
            width: auto;
        }
     
    }
    
    @media (max-width:620px) {
    
        
        .woocommerce form.cart .component-buttons .pizza-quantity input {
            width: 22px;
        }
        .food-components-wrapper h3 {
            font-size: 18px;
        }
    }
    
      